html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.Dropdown {
  position: absolute !important;
  z-index: 7000;
  width: auto;
  background-color: white; }

.rc-trigger-popup {
  position: absolute;
  left: -9999px;
  top: -9999px;
  z-index: 1000; }

.Menu.slds-dropdown {
  position: static;
  left: initial;
  transform: none;
  float: none;
  outline: 0; }
