/*
  This is from salesforce  https://www.lightningdesignsystem.com/design-tokens/#category-z-index
  We need a customized z-index chart because our header needs a higher z-index than the one there. 
  List availabe on LDS.
  $z-index-toast : 10000;
  $z-index-spinner: 9050
  $z-index-modal: 9000;
  $z-index-reminder: 8500;
  $z-index-overlay: 8000;
  $z-index-dropdown: 7000;
  $z-index-dialog: 6000;
  $z-index-popup: 5000;
  $z-index-sticky: 100;
  $z-index-docked: 4;
  $z-index-default: 1;
  $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.RadioWrapper,
.slds-scope .RadioWrapper {
  width: 100%;
  border: 1px #dddbda solid;
  border-radius: 8px;
  background-color: #f3f2f2;
  padding: 20px 15px;
  margin-bottom: 10px; }
  .RadioWrapper .slds-radio__label,
  .slds-scope .RadioWrapper .slds-radio__label {
    display: flex; }
  .RadioWrapper .slds-radio--faux,
  .slds-scope .RadioWrapper .slds-radio--faux {
    margin-top: 15px;
    flex: 0 0 16px; }
  .RadioWrapper .slds-form-element__label,
  .slds-scope .RadioWrapper .slds-form-element__label {
    display: flex; }
  .RadioWrapper .RadioWrapper__icon,
  .slds-scope .RadioWrapper .RadioWrapper__icon {
    padding: 0 15px 0 5px; }
    .RadioWrapper .RadioWrapper__icon .slds-icon-text-default,
    .slds-scope .RadioWrapper .RadioWrapper__icon .slds-icon-text-default {
      fill: #1589ee; }
  .RadioWrapper .RadioWrapper__icon .slds-icon-text-default,
  .slds-scope .RadioWrapper .RadioWrapper__icon .slds-icon-text-default {
    fill: #dddbda; }
  .RadioWrapper:hover, .RadioWrapper.RadioWrapper__checked,
  .slds-scope .RadioWrapper:hover,
  .slds-scope .RadioWrapper.RadioWrapper__checked {
    background-color: white; }
    .RadioWrapper:hover .slds-icon-text-default, .RadioWrapper.RadioWrapper__checked .slds-icon-text-default,
    .slds-scope .RadioWrapper:hover .slds-icon-text-default,
    .slds-scope .RadioWrapper.RadioWrapper__checked .slds-icon-text-default {
      fill: #1589ee; }
  .RadioWrapper.RadioWrapper__disabled,
  .slds-scope .RadioWrapper.RadioWrapper__disabled {
    background-color: #ecebea; }
    .RadioWrapper.RadioWrapper__disabled .slds-icon-text-default,
    .slds-scope .RadioWrapper.RadioWrapper__disabled .slds-icon-text-default {
      fill: #dddbda; }
  .RadioWrapper.RadioWrapper__verticalMiddle .slds-radio__label,
  .slds-scope .RadioWrapper.RadioWrapper__verticalMiddle .slds-radio__label {
    align-items: center; }
  .RadioWrapper.RadioWrapper__verticalMiddle .slds-form-element__label,
  .slds-scope .RadioWrapper.RadioWrapper__verticalMiddle .slds-form-element__label {
    align-items: center; }
  .RadioWrapper.RadioWrapper__verticalMiddle .slds-radio--faux,
  .slds-scope .RadioWrapper.RadioWrapper__verticalMiddle .slds-radio--faux {
    margin-top: 0; }

.slds-radio [type=radio]:checked + .slds-radio_faux:after,
.slds-radio [type=radio]:checked + .slds-radio--faux:after,
.slds-radio [type=radio]:checked ~ .slds-radio_faux:after,
.slds-radio [type=radio]:checked ~ .slds-radio--faux:after,
.slds-radio [type=radio]:checked + .slds-radio__label .slds-radio_faux:after,
.slds-radio [type=radio]:checked + .slds-radio__label .slds-radio--faux:after,
.slds-scope .slds-radio [type=radio]:checked + .slds-radio_faux:after,
.slds-scope .slds-radio [type=radio]:checked + .slds-radio--faux:after,
.slds-scope .slds-radio [type=radio]:checked ~ .slds-radio_faux:after,
.slds-scope .slds-radio [type=radio]:checked ~ .slds-radio--faux:after,
.slds-scope .slds-radio [type=radio]:checked + .slds-radio__label .slds-radio_faux:after,
.slds-scope .slds-radio [type=radio]:checked + .slds-radio__label .slds-radio--faux:after {
  background: #0070d2; }
