/*
  This is from salesforce  https://www.lightningdesignsystem.com/design-tokens/#category-z-index
  We need a customized z-index chart because our header needs a higher z-index than the one there. 
  List availabe on LDS.
  $z-index-toast : 10000;
  $z-index-spinner: 9050
  $z-index-modal: 9000;
  $z-index-reminder: 8500;
  $z-index-overlay: 8000;
  $z-index-dropdown: 7000;
  $z-index-dialog: 6000;
  $z-index-popup: 5000;
  $z-index-sticky: 100;
  $z-index-docked: 4;
  $z-index-default: 1;
  $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.Tree .Tree__branch {
  outline: 0; }
  .Tree .Tree__branch .toggle-icon {
    margin-top: 0; }

.Tree .Tree__leaf .item-icon {
  margin-left: 8px; }

.Tree .Tree__leaf .slds-text-color--weak {
  color: #706e6b; }

.Tree .Tree__leaf .leaf-data {
  display: flex;
  width: 100%;
  min-width: 0; }

.Tree .Tree__leaf .leaf-action {
  display: flex;
  align-items: center;
  margin: 0 10px; }
  .Tree .Tree__leaf .leaf-action .Button {
    align-self: auto;
    margin-top: initial;
    border: none; }
    .Tree .Tree__leaf .leaf-action .Button:focus {
      box-shadow: none; }

.Tree em {
  font-style: normal;
  background-color: #fff03f; }
