/*
  This is from salesforce  https://www.lightningdesignsystem.com/design-tokens/#category-z-index
  We need a customized z-index chart because our header needs a higher z-index than the one there. 
  List availabe on LDS.
  $z-index-toast : 10000;
  $z-index-spinner: 9050
  $z-index-modal: 9000;
  $z-index-reminder: 8500;
  $z-index-overlay: 8000;
  $z-index-dropdown: 7000;
  $z-index-dialog: 6000;
  $z-index-popup: 5000;
  $z-index-sticky: 100;
  $z-index-docked: 4;
  $z-index-default: 1;
  $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.Picklist {
  outline: none; }
  .Picklist .Input {
    overflow: hidden;
    text-overflow: ellipsis; }
    .Picklist .Input[readonly]::placeholder, .Picklist .Input::placeholder {
      font-style: italic;
      color: #969492; }
  .Picklist .Menu {
    position: absolute;
    z-index: 20; }

.slds-has-error .Picklist .Search .InputWrapper .Input {
  box-shadow: none;
  border-color: #dddbda; }

.slds-has-error .Picklist .Search .InputWrapper .slds-input__icon {
  fill: #dddbda;
  color: #dddbda; }

.slds-has-error .Picklist .Search .InputWrapper .Input.slds-input:focus,
.slds-has-error .Picklist .Search .InputWrapper .Input.slds-input:active {
  box-shadow: none; }

.slds-has-error .Input.slds-input:active,
.slds-has-error .Input.slds-input:focus {
  box-shadow: #c23934 0 0 0 1px inset, 0 0 3px #1589ee; }
