/*
  This is from salesforce  https://www.lightningdesignsystem.com/design-tokens/#category-z-index
  We need a customized z-index chart because our header needs a higher z-index than the one there. 
  List availabe on LDS.
  $z-index-toast : 10000;
  $z-index-spinner: 9050
  $z-index-modal: 9000;
  $z-index-reminder: 8500;
  $z-index-overlay: 8000;
  $z-index-dropdown: 7000;
  $z-index-dialog: 6000;
  $z-index-popup: 5000;
  $z-index-sticky: 100;
  $z-index-docked: 4;
  $z-index-default: 1;
  $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.ListTriggerControl .ListTriggerControl__header {
  margin-bottom: 10px; }

.ListTriggerControl .rt-td.DataGrid__cell {
  overflow: visible; }

.ListTriggerControl .ListTriggerControl__datagrid {
  display: contents;
  max-height: calc(100vh - 140px); }
  .ListTriggerControl .ListTriggerControl__datagrid .rt-tr-group:hover .RemoveItemColumn {
    display: flex; }
  .ListTriggerControl .ListTriggerControl__datagrid .RemoveItemColumn {
    padding-left: 10px;
    vertical-align: middle;
    display: none; }
  .ListTriggerControl .ListTriggerControl__datagrid .ListTriggerControl__datagrid_Selected {
    background-color: #FEF4E6; }
    .ListTriggerControl .ListTriggerControl__datagrid .ListTriggerControl__datagrid_Selected:hover {
      background-color: #f4f6fe; }
  .ListTriggerControl .ListTriggerControl__datagrid .ListTriggerControl__datagrid_Initial {
    background-color: #FFFFFF; }
    .ListTriggerControl .ListTriggerControl__datagrid .ListTriggerControl__datagrid_Initial:hover {
      background-color: #f4f6fe; }
