/*
  This is from salesforce  https://www.lightningdesignsystem.com/design-tokens/#category-z-index
  We need a customized z-index chart because our header needs a higher z-index than the one there. 
  List availabe on LDS.
  $z-index-toast : 10000;
  $z-index-spinner: 9050
  $z-index-modal: 9000;
  $z-index-reminder: 8500;
  $z-index-overlay: 8000;
  $z-index-dropdown: 7000;
  $z-index-dialog: 6000;
  $z-index-popup: 5000;
  $z-index-sticky: 100;
  $z-index-docked: 4;
  $z-index-default: 1;
  $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.InputWrapper__has-icon.slds-input-has-icon--left-right .slds-input__icon--right {
  left: auto; }

.InputWrapper__has-icon.slds-input-has-icon--right .slds-input__icon--right {
  right: 0.75rem; }

.InputWrapper.slds-size--block {
  width: 100%; }

.Input::placeholder {
  font-style: italic;
  color: #969492; }
