/*
  This is from salesforce  https://www.lightningdesignsystem.com/design-tokens/#category-z-index
  We need a customized z-index chart because our header needs a higher z-index than the one there. 
  List availabe on LDS.
  $z-index-toast : 10000;
  $z-index-spinner: 9050
  $z-index-modal: 9000;
  $z-index-reminder: 8500;
  $z-index-overlay: 8000;
  $z-index-dropdown: 7000;
  $z-index-dialog: 6000;
  $z-index-popup: 5000;
  $z-index-sticky: 100;
  $z-index-docked: 4;
  $z-index-default: 1;
  $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.DataGrid {
  min-height: 200px;
  max-height: 100vh;
  background-color: white;
  border-right: none;
  border-left: none; }
  .DataGrid.-no-row-border .rt-tbody .rt-tr-group {
    border-bottom: none; }
  .DataGrid.-col-border .rt-thead .rt-th:not(:last-child),
  .DataGrid.-col-border .rt-tbody .rt-td:not(:last-child) {
    border-right: 1px solid #e3e5ed; }
  .DataGrid.-col-sortable .rt-thead .rt-th {
    height: 35px;
    /**
    * inner element that gets arrow icon
    * non-resizable column config needs selector div:first-child
    */ }
    .DataGrid.-col-sortable .rt-thead .rt-th .rt-resizable-header-content,
    .DataGrid.-col-sortable .rt-thead .rt-th div:first-child {
      margin-right: 4px;
      display: inline-block;
      padding: 0 15px 0 0; }
    .DataGrid.-col-sortable .rt-thead .rt-th.DataGrid__header--align-center > div:first-child {
      padding-left: 15px; }
    .DataGrid.-col-sortable .rt-thead .rt-th.-cursor-pointer:not(.-sort-asc):not(.-sort-desc):hover .rt-resizable-header-content,
    .DataGrid.-col-sortable .rt-thead .rt-th.-cursor-pointer:not(.-sort-asc):not(.-sort-desc):hover div[class='']:first-child {
      background: no-repeat 100% center url(data:image/svg+xml;base64,); }
    .DataGrid.-col-sortable .rt-thead .rt-th.-cursor-pointer:not(.-sort-asc):not(.-sort-desc):hover .rt-resizer {
      background-color: #e3e5ed; }
    .DataGrid.-col-sortable .rt-thead .rt-th.-sort-asc .rt-resizable-header-content,
    .DataGrid.-col-sortable .rt-thead .rt-th.-sort-asc div[class='']:first-child {
      background: no-repeat 100% center url(data:image/svg+xml;base64,); }
    .DataGrid.-col-sortable .rt-thead .rt-th.-sort-asc:focus {
      border: 1px solid #005fb2; }
      .DataGrid.-col-sortable .rt-thead .rt-th.-sort-asc:focus .rt-resizer {
        background-color: #005fb2; }
    .DataGrid.-col-sortable .rt-thead .rt-th.-sort-desc .rt-resizable-header-content,
    .DataGrid.-col-sortable .rt-thead .rt-th.-sort-desc div[class='']:first-child {
      background: no-repeat 100% center url(data:image/svg+xml;base64,); }
    .DataGrid.-col-sortable .rt-thead .rt-th.-sort-desc:focus {
      border: 1px solid #005fb2; }
      .DataGrid.-col-sortable .rt-thead .rt-th.-sort-desc:focus .rt-resizer {
        background-color: #005fb2; }
  .DataGrid.DataGrid__list-view .Icon:not(.slds-button__icon) {
    fill: #c9c7c5;
    background-color: transparent; }
  .DataGrid.DataGrid__list-view .Icon:hover {
    cursor: pointer;
    fill: #969492;
    background-color: transparent; }
  .DataGrid.DataGrid--fixed-header .rt-table {
    overflow-y: hidden; }
  .DataGrid.DataGrid--fixed-header .rt-thead {
    overflow-x: hidden;
    overflow-y: scroll; }
  .DataGrid.DataGrid--fixed-header .rt-tbody {
    overflow-x: hidden;
    overflow-y: scroll; }
  .DataGrid .rt-tr-group {
    flex: 0 0 auto; }
  .DataGrid .rt-resizer {
    right: 0;
    width: 4px; }
  .DataGrid .rt-thead.-header,
  .DataGrid .rt-tbody .rt-tr-group {
    border-bottom: 1px solid #e3e5ed;
    box-shadow: none; }
  .DataGrid .rt-thead .rt-th.-sort-asc,
  .DataGrid .rt-thead .rt-td.-sort-asc,
  .DataGrid .rt-thead .rt-th.-sort-desc,
  .DataGrid .rt-thead .rt-td.-sort-desc {
    box-shadow: none; }
  .DataGrid .rt-thead .rt-tr {
    background-color: #fafaf9; }
  .DataGrid .rt-thead .rt-th {
    display: flex;
    padding: 0.75rem 5px;
    text-transform: uppercase;
    text-align: left; }
  .DataGrid .rt-tbody {
    position: relative;
    overflow: initial; }
    .DataGrid .rt-tbody .rt-td {
      display: flex;
      padding: 5px;
      min-height: 30px; }
      .DataGrid .rt-tbody .rt-td .Popover .Popover__trigger {
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden; }
      .DataGrid .rt-tbody .rt-td .Icon {
        margin: 1px 0 2px; }
  .DataGrid .rt-th {
    outline: none;
    letter-spacing: 0.0625rem; }
  .DataGrid .rt-tr {
    align-items: center; }
  .DataGrid .rt-thead .rt-th,
  .DataGrid .rt-tbody .rt-td {
    border-right: none;
    padding: 9px 5px; }
    .DataGrid .rt-thead .rt-th:first-child,
    .DataGrid .rt-tbody .rt-td:first-child {
      padding-left: 25px; }
    .DataGrid .rt-thead .rt-th:last-child,
    .DataGrid .rt-tbody .rt-td:last-child {
      padding-right: 25px; }
  .DataGrid .DataGrid__cell--align-center,
  .DataGrid .DataGrid__header--align-center {
    justify-content: center; }
  .DataGrid .DataGrid__cell--align-right,
  .DataGrid .DataGrid__header--align-right {
    flex-direction: column;
    align-items: flex-end; }
