/*
  This is from salesforce  https://www.lightningdesignsystem.com/design-tokens/#category-z-index
  We need a customized z-index chart because our header needs a higher z-index than the one there. 
  List availabe on LDS.
  $z-index-toast : 10000;
  $z-index-spinner: 9050
  $z-index-modal: 9000;
  $z-index-reminder: 8500;
  $z-index-overlay: 8000;
  $z-index-dropdown: 7000;
  $z-index-dialog: 6000;
  $z-index-popup: 5000;
  $z-index-sticky: 100;
  $z-index-docked: 4;
  $z-index-default: 1;
  $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.ColorPicker {
  position: relative; }
  .ColorPicker .ColorPicker__popover {
    position: absolute;
    top: 40px;
    z-index: 1000; }
  .ColorPicker .ColorPicker__cover {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0; }
  .ColorPicker .ColorPicker__selected-color {
    border: 1px solid #dddbda;
    border-radius: .25rem;
    padding: 0 1rem 0 .75rem;
    line-height: 1.875rem;
    width: 32px;
    height: 32px; }
