/*
  This is from salesforce  https://www.lightningdesignsystem.com/design-tokens/#category-z-index
  We need a customized z-index chart because our header needs a higher z-index than the one there. 
  List availabe on LDS.
  $z-index-toast : 10000;
  $z-index-spinner: 9050
  $z-index-modal: 9000;
  $z-index-reminder: 8500;
  $z-index-overlay: 8000;
  $z-index-dropdown: 7000;
  $z-index-dialog: 6000;
  $z-index-popup: 5000;
  $z-index-sticky: 100;
  $z-index-docked: 4;
  $z-index-default: 1;
  $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.Card {
  width: 340px;
  background-color: white;
  position: relative;
  border-radius: 4px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5); }
  .Card .Card__header__remove-button {
    position: absolute;
    right: 0;
    top: 0;
    padding: 10px; }
  .Card .Card__header,
  .Card .Card__content {
    padding: 10px; }
  .Card .Card__header {
    height: 40px;
    text-align: center; }
  .Card .Card__content {
    white-space: nowrap; }
    .Card .Card__content .Label {
      display: block; }
    .Card .Card__content .Card-content__alias-container,
    .Card .Card__content .Card-content__source-object {
      margin: 10px 0 10px 20px; }
    .Card .Card__content .Card-content__alias-container .InputWrapper {
      display: inline;
      margin-left: 5px; }
    .Card .Card__content .Card-content__alias-container .Checkbox + .InputWrapper .Input {
      width: 210px; }
  .Card .Card__footer .Grid {
    flex-wrap: wrap; }
  .Card .Card__footer .Card__footer-row {
    flex: 1 0 100%;
    border-top: 1px solid #c9c7c5;
    padding-top: 4px; }
    .Card .Card__footer .Card__footer-row .Text,
    .Card .Card__footer .Card__footer-row .Icon {
      line-height: 30px; }
    .Card .Card__footer .Card__footer-row .Text.slds-text-body--large {
      font-size: .875rem; }
  .Card .Card__footer .Icon {
    margin: 7px 8px; }
    .Card .Card__footer .Icon.Card__footer-icon--check {
      fill: #0070d2; }
    .Card .Card__footer .Icon.Card__footer-icon--error {
      fill: #c23934; }
  .Card .Card__footer .Card__footer-link {
    color: #1589ee;
    padding-right: 10px; }

.NewCard {
  border: 3px dashed #c9c7c5;
  padding: 10px;
  width: 340px;
  background-color: white;
  white-space: normal;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer; }
  .NewCard:hover {
    border: 3px dashed #0070d2; }
  .NewCard .NewCard__display {
    color: #b0adab;
    font-size: 15px;
    font-style: italic;
    padding: 20px;
    text-align: center; }

.Card--type-source-to-target {
  height: 400px; }
  .Card--type-source-to-target .Card__content {
    height: 240px; }
  .Card--type-source-to-target .Card__footer {
    height: 120px; }

.Card--type-standalone-edit {
  height: 320px; }
  .Card--type-standalone-edit .Card__content {
    height: 160px; }
  .Card--type-standalone-edit .Card__footer {
    height: 120px; }

.Card--type-standalone-create {
  height: 240px; }
  .Card--type-standalone-create .Card__content {
    height: 160px; }
  .Card--type-standalone-create .Card__footer {
    height: 40px; }

.Card--type-checklist {
  height: 280px; }
  .Card--type-checklist .Card__content {
    height: 160px; }
  .Card--type-checklist .Card__footer {
    height: 80px; }

.Card--type-mobile-view {
  height: 280px; }
  .Card--type-mobile-view .Card__content {
    height: 160px; }
  .Card--type-mobile-view .Card__footer {
    height: 80px; }

.Card--type-smart-doc {
  height: 240px; }
  .Card--type-smart-doc .Card__content {
    height: 160px; }
  .Card--type-smart-doc .Card__footer {
    height: 40px; }
