/*
  This is from salesforce  https://www.lightningdesignsystem.com/design-tokens/#category-z-index
  We need a customized z-index chart because our header needs a higher z-index than the one there. 
  List availabe on LDS.
  $z-index-toast : 10000;
  $z-index-spinner: 9050
  $z-index-modal: 9000;
  $z-index-reminder: 8500;
  $z-index-overlay: 8000;
  $z-index-dropdown: 7000;
  $z-index-dialog: 6000;
  $z-index-popup: 5000;
  $z-index-sticky: 100;
  $z-index-docked: 4;
  $z-index-default: 1;
  $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.DateInput {
  position: relative; }
  .DateInput .Input::-ms-clear {
    display: none; }
  .DateInput .Datepicker {
    position: absolute;
    z-index: 7000;
    left: 0;
    top: 32px; }
  .DateInput .DateInput__calendar-icon {
    z-index: 8; }

.Datepicker {
  width: 334px;
  outline: none;
  border: 1px solid #dddbda;
  border-radius: .25rem;
  background: #fff;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.1); }
  .Datepicker .Datepicker__day {
    outline: none; }
  .Datepicker .Datepicker__extension {
    padding: 8px;
    text-align: center; }

.slds-has-error .Datepicker .Select {
  border: 1px solid #d8dde6;
  box-shadow: none; }
