.TriggerControl {
  .TriggerControl__content {
    padding: 0.5rem;
    .Picklist__container {
      width: 100%;
      .Menu {
        width: 97.5%;
        position: fixed;
      }
    }
  }  
}

.TriggerControl_refresh { 
  //overflow-x: scroll;
  align-items: center;     
  display: flex;   
  justify-content: center;
  margin: 5px;     
  .Picklist__container { 
    .Picklist__input-icon{
      top: calc(50% - 7px);
      cursor: pointer;
    }  
   .Search__container {
    .Icon{
      top: calc(50% - 7px);     
    } 
   }
    
  }  
  }

  .TriggerControl_refreshNew { 
    //overflow-x: scroll;
    display: flex;   
    margin: 5px;     
    .Picklist__container { 
      .Picklist__input-icon{
        top: calc(50% - 7px);
        cursor: pointer;
      }  
     .Search__container {
      .Icon{
        top: calc(50% - 7px);     
      } 
     }
      
    }  
    }

  .TriggerControl_Highlight { 
    //overflow-x: scroll;
    align-items: center;     
    display: flex;   
    justify-content: center;
    margin: 5px;   
    background: #ff0000;  
    .Picklist__container { 
      .Picklist__input-icon{
        top: calc(50% - 7px);
        cursor: pointer;
      }  
     .Search__container {
      .Icon{
        top: calc(50% - 7px);     
      } 
     }
      
    }  
    }

  .UserValueDiv {
    width: inherit;  
  }

  .UserValueInnerDiv {
    align-items: center;     
    display: flex;   
    justify-content: center;
    margin: 5px;   
  }
  
