/*
  This is from salesforce  https://www.lightningdesignsystem.com/design-tokens/#category-z-index
  We need a customized z-index chart because our header needs a higher z-index than the one there. 
  List availabe on LDS.
  $z-index-toast : 10000;
  $z-index-spinner: 9050
  $z-index-modal: 9000;
  $z-index-reminder: 8500;
  $z-index-overlay: 8000;
  $z-index-dropdown: 7000;
  $z-index-dialog: 6000;
  $z-index-popup: 5000;
  $z-index-sticky: 100;
  $z-index-docked: 4;
  $z-index-default: 1;
  $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.Badge {
  margin: 0 3px;
  display: inline-block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  min-width: 89px;
  max-width: 180px;
  text-align: center;
  cursor: default; }
  .Badge.slds-badge_lightest {
    height: 25px;
    border: 1px solid #dddbda;
    background-color: white;
    font-size: 0.75rem;
    text-transform: none;
    letter-spacing: normal; }
    .Badge.slds-badge_lightest:hover {
      background-color: #e3e5ed; }
  .Badge .Badge__label {
    display: inline-block;
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis; }
  .Badge .Badge__label--with-icon {
    width: calc(100% - 20px);
    overflow: hidden;
    text-overflow: ellipsis; }
  .Badge .Badge__close {
    float: right; }
    .Badge .Badge__close:focus, .Badge .Badge__close:active {
      outline: 0;
      box-shadow: none; }
    .Badge .Badge__close .Icon {
      position: absolute;
      top: 1px;
      right: -2px; }

.BadgeList {
  margin-top: 6px;
  display: inline-block;
  position: relative; }
  .BadgeList .Badge {
    top: 0;
    display: inline-block;
    position: relative;
    z-index: 10;
    margin-left: 0;
    margin-right: 6px;
    margin-bottom: 4px; }
  .BadgeList .Badge__stack {
    position: absolute;
    left: 0;
    top: 0; }
  .BadgeList.BadgeList--stacked:hover .Badge {
    background-color: #e3e5ed; }
