/*
  This is from salesforce  https://www.lightningdesignsystem.com/design-tokens/#category-z-index
  We need a customized z-index chart because our header needs a higher z-index than the one there. 
  List availabe on LDS.
  $z-index-toast : 10000;
  $z-index-spinner: 9050
  $z-index-modal: 9000;
  $z-index-reminder: 8500;
  $z-index-overlay: 8000;
  $z-index-dropdown: 7000;
  $z-index-dialog: 6000;
  $z-index-popup: 5000;
  $z-index-sticky: 100;
  $z-index-docked: 4;
  $z-index-default: 1;
  $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.Checkbox .slds-checkbox--faux {
  margin-right: .5rem; }

.Checkbox.slds-checkbox [type=checkbox]:checked + .slds-checkbox_faux:after,
.Checkbox.slds-checkbox [type=checkbox]:checked + .slds-checkbox--faux:after,
.Checkbox.slds-checkbox [type=checkbox]:checked ~ .slds-checkbox_faux:after,
.Checkbox.slds-checkbox [type=checkbox]:checked ~ .slds-checkbox--faux:after,
.Checkbox.slds-checkbox [type=checkbox]:checked + .slds-checkbox__label .slds-checkbox_faux:after,
.Checkbox.slds-checkbox [type=checkbox]:checked + .slds-checkbox__label .slds-checkbox--faux:after,
.slds-scope .Checkbox.slds-checkbox [type=checkbox]:checked + .slds-checkbox_faux:after,
.slds-scope .Checkbox.slds-checkbox [type=checkbox]:checked + .slds-checkbox--faux:after,
.slds-scope .Checkbox.slds-checkbox [type=checkbox]:checked ~ .slds-checkbox_faux:after,
.slds-scope .Checkbox.slds-checkbox [type=checkbox]:checked ~ .slds-checkbox--faux:after,
.slds-scope .Checkbox.slds-checkbox [type=checkbox]:checked + .slds-checkbox__label .slds-checkbox_faux:after,
.slds-scope .Checkbox.slds-checkbox [type=checkbox]:checked + .slds-checkbox__label .slds-checkbox--faux:after {
  border-bottom-color: #0070d2;
  border-left-color: #0070d2; }
