/*
  This is from salesforce  https://www.lightningdesignsystem.com/design-tokens/#category-z-index
  We need a customized z-index chart because our header needs a higher z-index than the one there. 
  List availabe on LDS.
  $z-index-toast : 10000;
  $z-index-spinner: 9050
  $z-index-modal: 9000;
  $z-index-reminder: 8500;
  $z-index-overlay: 8000;
  $z-index-dropdown: 7000;
  $z-index-dialog: 6000;
  $z-index-popup: 5000;
  $z-index-sticky: 100;
  $z-index-docked: 4;
  $z-index-default: 1;
  $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.LookupSearch__icon-wrapper {
  cursor: pointer;
  outline: none; }

.Lookup .Lookup__search {
  flex-wrap: nowrap;
  -ms-flex-wrap: nowrap;
  -webkit-flex-wrap: nowrap; }
  .Lookup .Lookup__search .slds-lookup[data-scope="multi"] .react-slds-lookup-scope-selector {
    min-width: 3rem; }
    .Lookup .Lookup__search .slds-lookup[data-scope="multi"] .react-slds-lookup-scope-selector .slds-dropdown-trigger {
      margin-left: 0; }
      .Lookup .Lookup__search .slds-lookup[data-scope="multi"] .react-slds-lookup-scope-selector .slds-dropdown-trigger .slds-button {
        padding: 0 0.25rem; }
  .Lookup .Lookup__search .slds-lookup[data-scope="multi"] .slds-box--border {
    background-color: white; }
    .Lookup .Lookup__search .slds-lookup[data-scope="multi"] .slds-box--border .slds-input--bare {
      height: 2.15rem;
      width: 100%; }
  .Lookup .Lookup__search .slds-lookup[data-scope="multi"] .slds-box--border.react-slds-box-disabled {
    background-color: #ecebea;
    border-color: #c9c7c5;
    cursor: not-allowed; }
  .Lookup .Lookup__search .slds-lookup[data-scope="multi"] .slds-input::placeholder {
    color: #b0adab;
    font-style: italic; }

.Lookup .Lookup__menu {
  min-width: unset;
  max-width: unset;
  position: absolute;
  top: 30px; }

.Lookup .Lookup__menu.v-align {
  bottom: 100%; }

.Lookup .Lookup__menu.h-align {
  left: auto;
  right: 0; }

.Lookup .Icon use {
  pointer-events: auto;
  cursor: pointer; }
