/*
  This is from salesforce  https://www.lightningdesignsystem.com/design-tokens/#category-z-index
  We need a customized z-index chart because our header needs a higher z-index than the one there. 
  List availabe on LDS.
  $z-index-toast : 10000;
  $z-index-spinner: 9050
  $z-index-modal: 9000;
  $z-index-reminder: 8500;
  $z-index-overlay: 8000;
  $z-index-dropdown: 7000;
  $z-index-dialog: 6000;
  $z-index-popup: 5000;
  $z-index-sticky: 100;
  $z-index-docked: 4;
  $z-index-default: 1;
  $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.Button.Button--type-icon-container:focus, .Button.Button--type-icon-bare:focus {
  box-shadow: none; }

.Button,
.slds-scope .Button {
  max-width: 300px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap; }
  .Button.Button--type-brand:not([disabled]),
  .slds-scope .Button.Button--type-brand:not([disabled]) {
    background-color: #0070d2;
    border-color: #0070d2; }
  .Button.Button--type-outline-brand:not([disabled]),
  .slds-scope .Button.Button--type-outline-brand:not([disabled]) {
    border-color: #0070d2; }
  .Button.Button--type-neutral:not([disabled]),
  .slds-scope .Button.Button--type-neutral:not([disabled]) {
    color: #0070d2; }
  .Button.Button--type-neutral-gray,
  .slds-scope .Button.Button--type-neutral-gray {
    padding: 0 1rem;
    text-align: center;
    vertical-align: middle;
    color: #514f4d;
    border: 1px solid #c9c7c5;
    transition: border .15s linear;
    background-color: #ecebea; }
    .Button.Button--type-neutral-gray:hover,
    .slds-scope .Button.Button--type-neutral-gray:hover {
      background-color: #dddbda; }
    .Button.Button--type-neutral-gray:disabled,
    .slds-scope .Button.Button--type-neutral-gray:disabled {
      background-color: #ecebea;
      color: #c9c7c5; }
